import Vue from 'vue';
import Vuex from 'vuex'
import state from './state'
// 1. 定义 (路由) 组件。
// 可以从其他文件 import 进来

Vue.use(Vuex)

const store = new Vuex.Store({
    state: state,
    getters:{
        getPhone(state){
            return state.phone
        },
        getName(state){
            return state.name
        },
        getProduct(state){
            return state.product
        },
    },
    // 同步
    mutations: {
        increment (state) {
            state.count++
        },
        phone (state, phone) {
            state.phone = phone
        },
        product (state, product) {
            state.product = product
        }
    },
    // 异步
    actions:{},
    modules:{}
})

export default store;
