<template>
    <div>
        home / {{info}}
        <div v-for="category in categories" v-bind:key="category.id">
            <h3>{{category.name}}</h3>
            <van-grid column-num="3">
                <van-grid-item v-for="product in category.products" v-bind:key="product.uri" >
                    <router-link :to="'/product/'+ product.uri">
                        <van-image :src="product.cover" fit="cover"/>
                        <p>{{product.name}}</p>
                    </router-link>
                </van-grid-item>
            </van-grid>
        </div>

    </div>
</template>

<script>
    import { Grid, GridItem, Image } from 'vant';
    import {home} from "@/api/api";
    export default {
        name: "Home.vue",
        data () {
            return {
                categories : [],
                info: null,
            }
        },
        mounted () {
            home().then(response => {
                    this.categories = response.data.data.categories
                })
        },
        components: {
            [Grid.name]: Grid,
            [GridItem.name]: GridItem,
            [Image.name]: Image,
        },
    }
    console.log(process.env.VUE_APP_SECRET)
</script>

