<template>
    <div>
        <van-image :src="product.cover" width="10rem" fit="cover"/>
        <input><button>{{uri}}</button>

        <div>
            <van-popup v-model="show" position="bottom" :style="{ height: '30%' }">
                <router-link :to="'/product/' + uri + '/appointment'">appointment</router-link>
            </van-popup>
        </div>
        <van-row>
            <van-col span="12">重选拍照类型</van-col>
            <van-col span="12"><van-cell is-link @click="showPopup">下一步</van-cell></van-col>
        </van-row>
    </div>
</template>

<script>
    import {product} from "@/api/api";
    import {Image, Col, Row, Cell,Popup } from 'vant';

    export default {
        name: "Product",
        data: function(){
            return {
                product: {},
                show: false,
                uri: this.$route.params.uri
            };
        } ,
        methods:{
            // btnPhone:function () {
            //     this.$store.commit('phone', this.phone)
            // },
            showPopup() {
                this.show = true;
            },
        },
        mounted () {
            product(this.$route.params.uri).then(response => {
                this.product = response.data.data.product
              this.$store.commit('product', this.product)
            })
        },
        components: {
            [Image.name]: Image,
            [Col.name]: Col,
            [Row.name]: Row,
            [Cell.name]: Cell,
            [Popup.name]: Popup,
        },
    }
</script>

