import Vue from 'vue';
import VueRouter from "vue-router";
import Home from "@/pages/Home";
import Product from "@/pages/Product";
import Packages from "@/pages/Packages";
import Appointment from "@/pages/Appointment";
import Contact from "@/pages/Contact";
import Order from "@/pages/Order";
import MyOrders from "@/pages/my/Orders";
// 1. 定义 (路由) 组件。
// 可以从其他文件 import 进来

Vue.use(VueRouter);

// 2. 定义路由
// 每个路由应该映射一个组件。 其中"component" 可以是
// 通过 Vue.extend() 创建的组件构造器，
// 或者，只是一个组件配置对象。
// 我们晚点再讨论嵌套路由。
const routes = [
    {path: '/', component: Home},
    {path: '/product/:uri', component: Product},
    {path: '/product/:uri/packages', component: Packages},
    {path: '/product/:uri/appointment', component: Appointment},
    {path: '/product/:uri/contact', component: Contact},
    {path: '/product/:uri/order', component: Order},
    {path: '/my/orders', component: MyOrders},
]

// 3. 创建 router 实例，然后传 `routes` 配置
// 你还可以传别的配置参数, 不过先这么简单着吧。
const router = new VueRouter({
    // mode: 'history',
    routes // (缩写) 相当于 routes: routes
})

export default router;
